export default {
	data() {
		return {
			isEdit: false,
			isSelectAll: false,
			goodsList: [
				{
					name: '精选产品名称',
					des: '新鲜营养绿色无污染非转基因食材非转基因食材非转基因食材',
					price: 10.00,
					count: 1,
					isSelect: false,
					src: '../../static/images/cartActive.png',
				},
				{
					name: '精选产品名称',
					des: '新鲜营养绿色无污染非转基因食材非转基因食材非转基因食材',
					price: 5.00,
					count: 4,
					isSelect: false,
					src: '../../static/images/cartActive.png',
				},
				{
					name: '精选产品名称',
					des: '新鲜营养绿色无污染非转基因食材非转基因食材非转基因食材',
					price: 8.00,
					count: 6,
					isSelect: false,
					src: '../../static/images/cartActive.png',
				}
			],
			totalMoney: 0,
		};
	},
	watch: {
		goodsList: {
			handler(list) {
				let totalMoney = 0;
				list.forEach((item) => {
					if(item.isSelect) {
						totalMoney += item.count * item.price
					}
				})
				this.totalMoney = totalMoney;
				const isSelectAll = list.every((item) => {
					return item.isSelect
				})
				if(isSelectAll) {
					this.isSelectAll = true;
				} else {
					this.isSelectAll = false;
				}
			},
			immediate: true,
			deep: true,
		},
	},
	methods: {
		onToggleEdit(type) {
			this.isEdit = !this.isEdit;
		},
		onToggleSelectAll() {
			this.isSelectAll = !this.isSelectAll;
			if(this.isSelectAll) {
				this.goodsList.forEach((item) => {
					item.isSelect = true;
				})
			} else {
				this.goodsList.forEach((item) => {
					item.isSelect = false;
				})
			}
		},
		onToggleSelectItem(index) {
			this.goodsList[index].isSelect = !this.goodsList[index].isSelect;
		},
		onMinusNum(index) {
			if(this.goodsList[index].count > 0) {
				this.goodsList[index].count--;
			} else {
				this.goodsList[index].count = 0;
			}
		},
		onAddNum(index) {
			this.goodsList[index].count++;
		},
		onDelete() {
			const goodsList = this.goodsList.filter((item) => !item.isSelect)
			this.goodsList = goodsList;
		},
	}
};
